import React from 'react';
import { Table, Progress } from 'reactstrap';
import User from '../data/User';
import 'bootstrap/dist/css/bootstrap.min.css';

function Progresso() {

    function Tabela() {

        return User.map(user => {
            return <tr key={user.nome}>
                <td className="align-middle text-center">
                    {user.nome}
                </td>
                <td className="align-middle text-center">
                    {user.data}
                </td>
                <td className="align-middle text-center">
                    <Progress color="info" value={user.progress}/>
                    {user.progress}
                </td>
                <td className="align-middle text-center">
                    {user.progress}%
                </td>
            </tr>
        }); // Mapeia tudo que esta dentro do user e puxar direto
    }
    return (
            <Table>
                <thead>
                    <tr>
                        <th className="align-middle text-center">Nome</th>
                        <th className="align-middle text-center">Data ðŸ“„</th>
                        <th className="align-middle text-center">Progresso ðŸ“Š</th>
                        <th className="align-middle text-center">%</th>
                    </tr>
                </thead>
                <tbody>
                    {Tabela()}
                </tbody>
            </Table>

    );
}

export default Progresso;