import React from 'react'
import { Container, Table, Card, CardHeader, CardBody } from 'reactstrap'
import 'bootstrap/dist/css/bootstrap.min.css'
import Progresso from './Progresso'
import '../style/Tabela.css'


function Tabela(props) {

    return (
        <Card body inverse style={{ backgroundColor: '#919191',color:'white', borderColor: 'blue' }} className="tabela mx-auto">
            <CardHeader className="text-capitalize align-middle text-center">
                <h1>Novos Usuarios</h1>
                </CardHeader>
            <CardBody>
                <Progresso/>
            </CardBody>
        </Card>
    )
}

export default Tabela